// 封装axios
// 1.引入第三方依赖
import axios from 'axios'
import store from '../store/index.js'

// 旧方法2.配置默认请求地址
// 通过defaults.baseURL创建默认请求地址有个缺点：这个项目只能有一个默认地址
// axios.defaults.baseURL = 'http://toutiao.itheima.net//'

// 新方法2.通过axios的create方法，可以创建多个请求地址，api需要使用的时候进行选择
// create方法是工作中最常用的
const request = axios.create({
  // baseURL: 'http://toutiao.itheima.net/'
  baseURL: 'https://toutiao.itheima.net/' // 线上地址
})

// 请求拦截器  ---在该拦截器中可以统一处理前端发送的接口参数
request.interceptors.request.use(config => {
  /*
    config：本次请求的配置对象(headers,data,params,url,method)
  */
  // 通过满足已登录的状态（store里的user）来判断，给满足条件的接口请求添加headers
  // {user} = tore.state  解构赋值的写法
  // 大部分的后端接口，它们只关注有用的token，但是传了没有用的token对于后端是没有影响
  const { user } = store.state
  if (user && user.token) {
    config.headers.Authorization = `Bearer ${user.token}`
  }
  return config
}, error => {
  // 如果请求出错了，抛出异常(固定写法)
  return Promise.reject(error)
})
// 3.导出
export default request
