// 与评论相关接口
import request from '../utils/request.js'

/**
 * 评论请求模块
 * params  type评论类型 a对文章评论 c对评论回复
 * source 源id 文章id 或者 评论id
 * offset 获取评论的偏移量  ---- 简单来说就是页码，可以不传，传null表示从头获取
 *
*/
export const getComments = params => request({
  url: '/v1_0/comments',
  method: 'GET',
  params
})

/**
 *
 * 评论点赞
 * -------------------
 * @param target  评论ID
 * -------------------
*/
export const addCommentLick = target => request({
  url: '/v1_0/comment/likings',
  method: 'POST',
  data: {
    target: target
  }
})

/**
 *
 * 取消评论点赞
 * -------------------
 * @param target  评论ID
 * -------------------
*/
export const deleteCommentLick = target => request({
  url: `/v1_0/comment/likings/${target}`,
  method: 'DELETE'
})

/**
 *
 * 发布文字评论 或 评论回复
 * -------------------
 * @param target 文字id或评论id
 * content 评论内容
 * art_id 非必要 -如果target是评论id,那么需要将文章id放到该属性上，如果target
 * -------------------
*/
export const addComment = data => request({
  url: '/v1_0/comments',
  method: 'POST',
  data
})
