// 关键字搜索相关请求模块
import request from '../utils/request.js'

/**
 * 联想列表搜索
 * qarams 搜索文本字符串
 * q后端接口提供的
*/
export const getSearchSuggestions = qarams => request({
  url: '/v1_0/suggestion',
  method: 'GET',
  params: {
    q: qarams
  }
})

/**
 * 联想列表搜索
 * qarams 搜索文本字符串
 * q后端接口提供的
*/
export const getSearchResult = params => request({
  url: '/v1_0/search',
  method: 'GET',
  params
})
