// 与文章相关的API
import request from '../utils/request.js'
/**
 *
 * 获取用户频道列表
 * -------------------
 * @param channel_id  频道ID
 * @param timestamp    时间戳
 * -------------------
*/
export const getArticles = params => request({
  url: '/v1_0/articles',
  method: 'GET',
  params
})

/**
 *
 * 获取文章详情
 * -------------------
 * @param article_id  频道ID
 * -------------------
*/
export const getArticleById = articleId => request({
  url: `/v1_0/articles/${articleId}`,
  method: 'GET'
})

/**
 *
 * 关注用户
 * -------------------
 * @param target  用户ID
 * -------------------
*/
export const addFollow = target => request({
  url: '/v1_0/user/followings',
  method: 'POST',
  data: {
    target: target
  }
})

/**
 *
 * 取消关注用户
 * -------------------
 * @param target  用户ID
 * -------------------
*/
export const deleteFollow = target => request({
  url: `/v1_0/user/followings/${target}`,
  method: 'DELETE'
})

