// 与频道相关的API
import request from '../utils/request.js'

export const getAllChannels = () => request({
  url: '/v1_0/channels',
  method: 'GET'
})

/**
 * 添加用户频道
*/
export const addUserChannel = (data) => request({
  url: '/v1_0/user/channels',
  method: 'PATCH',
  data
})

/**
 * 删除频道
 * target 表示需要删除的判断id。接口文档
 * */
export const deleteUserChannel = (target) => request({
  url: `/v1_0/user/channels/${target}`,
  method: 'DELETE'
})
