// 用户相关的请求模块
import request from '../utils/request.js'
// 手动引入store/index,js
// import store from '../store/index.js'

/**
 * 用户登录接口
 * -------------------
 * @params mobile 手机号
 * @params code 验证码
 * -------------------
*/
export const login = data => request({
  url: '/v1_0/authorizations',
  method: 'POST',
  data: data
})
/* 400 请求参数错误 403  refresh_token 未携带或已过期 507 数据库错误 201 OK */

/**
 * 发送手机验证
 * -------------------
 * @url mobile 手机号
 * -------------------
*/
export const getSmsCode = mobile => request({
  // /v1_0/sms/codes/:mobile
  url: `/v1_0/sms/codes/${mobile}`,
  method: 'get'
})

/**
 *
 * 获取指定用户信息
 * -------------------
 * @url target
 * -------------------
*/
export const getUserInfo = () => request({
  // /v1_0/sms/codes/:mobile
  url: '/v1_0/user',
  method: 'GET'
/* 每次都要设置token很麻烦，可以在utils的request中设置请求拦截器
headers: {
    // 注意 ：一般项目中，除登录以外，大部分接口都需要TOKEN授权才可以访问
    // 注意：使用Authorization时Bearer后面是有空格的
    Authorization: `Bearer ${store.state.user.token}`
  } */
})

/**
 *
 * 获取用户频道列表
 * -------------------
 * @url target
 * -------------------
*/
export const getUserChannels = () => request({
  url: '/v1_0/user/channels',
  method: 'GET'
/* 每次都要设置token很麻烦，可以在utils的request中设置请求拦截器
headers: {
    // 注意 ：一般项目中，除登录以外，大部分接口都需要TOKEN授权才可以访问
    // 注意：使用Authorization时Bearer后面是有空格的
    Authorization: `Bearer ${store.state.user.token}`
  } */
})

/**
 * 获取用户个人信息
 * -------------------
*/
export const getUserProfile = () => request({
  url: '/v1_0/user/profile',
  method: 'GET'
})

/**
 * 编辑用户个人信息
 * -------------------
 * name  昵称
 * photo 头像 base64编码后的
 * gender 性别 0表示男 1表示女
 * birthday 生日 格式要求字符串2022-08-03
 * -------------------
*/
export const updateUserProfile = data => request({
  url: '/v1_0/user/profile',
  method: 'PATCH',
  data
})

/**
 * 编辑用户个人信息头像
 * -------------------
 * photo 头像 base64编码后的 file数据类型，一切文件传输都需要用FormData实例对象进行转换
 * -------------------
*/
export const updateUserphoto = data => request({
  url: '/v1_0/user/photo',
  method: 'PATCH',
  data
})
